module.exports = [[module.id, "/* Importar fuentes custom */\n@font-face{\n\tfont-family: \"yanone\";\n\tsrc: url('YanoneKaffeesatz-Light.ttf');\n}\n@font-face{\n\tfont-family: \"finger\";\n\tsrc: url('FingerPaint-Regular.ttf');\n}\n/* ******************************* Estilos generales ********************************** */\n/* Margen y padding 0px para todos los elementos, así poder controlar 100% los tamaños y márgenes de los \n   elementos */\n*{\n\tmargin: 0px;\n\tpadding: 0px;\n}\nbody{\n\tfont-family: \"yanone\", Arial, Helvetica;\n\tfont-size: 20px;\n\tbackground: url('pattern.png');\n\tbackground-attachment: fixed;\n}\n/* Limpiamos los flotados entre los diferentes elementos */\n.clearfix{\n\tfloat: none;\n\tclear: both;\n}\n/* Header - Cabezera */\nheader{\n\tdisplay: block;\n\twidth: 100%;\n\theight: 90px;\n\tbackground: rgba(53, 103, 164, 0.8);\n\tposition: fixed;\n\ttext-transform: uppercase;\n}\n/* Menú de navegación */\nheader ul{\n\tdisplay: block;\n\twidth: 50%;\n\theight: inherit; \n\tmargin: 0px auto;\n\tmargin-top: -5px;\n}\nheader ul li{\n\tdisplay: inline-block;\n\tmargin: 0px auto;\n\ttext-align: center;\n\tline-height: 90px;\n}\nheader a{\n\tdisplay: block;\n\tpadding-right: 20px;\n\tpadding-left: 20px;\n\theight: inherit;\n\ttext-decoration: none;\n\tcolor: white;\n\ttransition: all 300ms;\n}\n/* Le ponemos fondo negro a los enlaces del menú al pasar el ratón por encima. Y\n   le añadimos la clase .actived al estár seleccionado en el menú. Esto lo hace la directiva\n   [routerLinkActive] aplicada en la vista del menú app.component.html */\nheader a:hover, .actived{\n\tbackground: rgba(0,0,0,0.8);\n}\n/* Evitamos que el logo tenga la transición cuando pasemos el cursor por encima */\nheader #logo a:hover{\n\tbackground: transparent;\n}\n/*  Estilos Logotipo Menú */\n#logo{\n\tfont-family: finger;\n\tfont-size: 35px;\n}\n/* Metemos un efecto de transición al enlace del color y una transformación, de\n   500ms ambos, definimos la transición en #logo a:hover{} */\n#logo a{\n\ttransition: color 500ms, tranform 500ms;\n}\n/* Al logo como es un enlace también le ponemos fondo transparente para que parezca\nque no tiene función de hover. */\n#logo a:hover{\n\tbackground: transparent;\n\tcolor: black;\n\ttransform: scale(1.50, 1.50);\n}\n/* Contenedor principal de la página - Content */\n#content{\n\tmin-height: 600px;\n\theight: auto;\n\twidth: 75%;\n\t/* centramos el elemento */\n\tmargin: 0px auto;\n\tbox-shadow: 0px 0px 40px black;\n\tborder-left: 1px solid #b7b7b7;\n\tborder-right: 1px solid #b7b7b7;\n\tbackground: rgba(255, 255, 255, 0.7);\n\tpadding-top: 120px;\n}\n/* Estilos Contenedor de cada página */\n.container{\n\tpadding: 50px;\n\tpadding-top: 10px;\n}\n.container h2{\n\tdisplay: block;\n\tborder-bottom: 1px solid #ccc;\n\tpadding-bottom: 10px;\n\tmargin-bottom: 15px;\n}\n/* Pié de página - Footer */\nfooter{\n\tdisplay: block;\n\twidth: 75%;\n\theight: 60px;\n\tmargin: 0px auto;\n\tbackground: rgba(53, 103, 164, 0.8);\n\ttext-align: center;\n\tpadding-top: 30px;\n\tfont-weight: bold;\n\tcolor: white;\n}\n/* Estilos formularios de la página */\nform{\n\twidth: 80%;\n}\nform label{\n\tdisplay: block;\n\tmargin-top: 10px;\n\tmargin-bottom: 5px;\n\twidth: 100%;\n}\nform input[type=\"text\"],\nform input[type=\"number\"],\nform input[type=\"email\"],\nform textarea{\n\twidth: 40%;\n\tpadding: 5px;\n}\nform textarea{\n\tmin-height: 60px;\n}\nform button,\nform input[type=\"submit\"]{\n\tdisplay: block;\n\tfont-size: 17px;\n\tpadding: 5px;\n\tmargin-top: 15px;\n\tcolor: white;\n\tbackground: rgba(53, 103, 164, 1);\n\tborder: 1px solid rgba(13, 67, 133, 1);\n\tcursor: pointer;\n}\nform button:hover,\nform input[type=\"submit\"]:hover{\n\tbackground: rgba(53, 103, 164, 0.8);\n}\nform input[disabled]{\n\topacity: 0.4;\n\tcursor: not-allowed;\n}\n.formError{\n\tborder: 1px solid red;\n\tfont-size: 13px;\n\tfont-weight: bold;\n\tpadding: 3px;\n\tbackground: rgba(255, 0, 0, 0.7);\n\tcolor: white;\n\tmargin-left: 5px;\n}\n/* Mensajes success y failed formularios */\n.message{\n\twidth: 50%;\n\tpadding: 5px;\n\tborder: 1px solid #eee;\n\tmargin-top: 5px;\n}\n.message a{\n\tcolor: white;\n}\n.success{\n\tbackground: green;\n\tcolor: white;\n}\n.failed{\n\tbackground: red;\n\tcolor: white;\n}\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]